.is-hidden {
    // stylelint-disable-next-line declaration-no-important
    display: none !important;
}

.for-desktop {
    @include mobile {
        // stylelint-disable-next-line declaration-no-important
        display: none !important;
    }
}

.for-main {
    display: none;
    [data-page="main"] & {
        // stylelint-disable-next-line declaration-no-important
        display: flex !important;
    }
}

.for-article {
    display: none;
    [data-page="article"] & {
        // stylelint-disable-next-line declaration-no-important
        display: block !important;
    }
}

.for-mobile {
    @include desktop {
        // stylelint-disable-next-line declaration-no-important
        display: none !important;
    }
}

.is-disabled {
    cursor: default;
    pointer-events: none;
}

.none {
    display: none;
}

.clearfix {
    overflow: auto;
    &::after {
        content: "";
        display: table;
        clear: both;
    }
}