.header {
    position: fixed;
    z-index: 3;
    left: 0;
    top: 0;
    width: 100%;
    height: vw(136);
    padding: vw(17) 0 vw(20);
    background-color: $color-white;
    &__container {
        padding: 0 vw(100) 0 vw(125);
        @include mobile {
            padding: 0 vmin(48) 0 vmin(55);
        }
    }
    &__row {
        display: flex;
        align-items: center;
        @include mobile {
            align-items: unset;
            justify-content: space-between;
        }
    }
    &__left {
        display: flex;
    }
    &__logo {
        width: vw(99);
        height: vw(98);
        @include mobile {
            min-width: vmin(99);
            min-height: vmin(98);
        }
    }
    &__format {
        position: relative;
        margin: 0 vw(165) 0 vw(58);
        max-width: vw(225);
        font-family: $font-family-myriadPRO;
        font-size: vw(16);
        font-weight: 400;
        text-transform: uppercase;
        &::before {
            position: absolute;
            left: vw(-25);
            top: vw(-5);
            content: '';
            width: vw(1);
            height: vw(42);
            background-color: $color-black;
            @include mobile {
                left: vmin(-25);
                top: 0;
                width: vmin(2);
                height: vmin(64);
            }
        }
        @include mobile {
            max-width: vmin(370);
            padding-top: vmin(5);
            margin: vmin(10) 0 0 vmin(80);
            font-size: vmin(24);
        }
    }
    &__menu {
        @include flex-center(center, space-between);
        margin-right: vw(97);
        width: vw(306);
        @include mobile {
            display: none;
        }
    }
    &__links {
        font-family: $font-family-proximanovat;
        font-size: vw(16);
        font-weight: 100;
        color: $color-black;
    }
    &__bin {
        display: flex;
        &_icon {
            @include position (relative);
            @include flex-center;
            @include sizing (vw(62), vw(62));
            @include border (none, 50%);
            @include color ($color-orange);
            padding: 0 vw(17);
            svg {
                display: block;
                width: 100%;
                height: 100%;
            }
            @include mobile {
                @include sizing (vmin(82), vmin(82));
                padding: 0 vmin(20) 0 vmin(20);
            }
        }
        &-count {
            @include position (absolute, auto, vw(-6), vw(-6));
            @include flex-center;
            @include sizing (vw(26), vw(26), vw(16));
            @include border (none, 50%);
            @include color ($color-white);
            font-family: $font-family-segoiUI;
            box-shadow: 0 0 vw(5) vw(0.1) $color-shadow;
            @include mobile {
                @include position (absolute, auto, vmin(-6), vmin(-6));
                @include sizing (vmin(35), vmin(35), vmin(23));
                @include color (#ab1717, $color-white);
            }
        }
    }
    &__bin_info {
        margin-left: vw(26);
        padding-top: vw(11);
        &-type {
            margin-bottom: vw(8);
            font-family: $font-family-ProximaNova;
            font-size: vw(16);
            font-weight: 100;
        }
        &-food {
            font-family: $font-family-ProximaNova;
            font-size: vw(18);
            font-weight: 800;
        }
        @include mobile {
            display: none;
        }
    }
    &__contact {
        margin-left: vw(92);
        font-family: $font-family-segoiUI;
        font-size: vw(19.5);
        font-weight: bold;
        a {
            color: $color-black;
        }
        &_phone:first-child {
            margin-bottom: vw(18);
        }
    }
    &__contact.for-mobile {
        margin-left: 0;
        padding: vmin(45) 0 vmin(20);
        font-size: vmin(27);
        text-align: center;
    }
    &__order-time {
        text-align: center;
        font-size: vmin(24);
        font-family: $font-family-myriadPRO;
    }
    @include mobile {
        height: auto;
        max-height: vmin(330);
        padding: vmin(45) 0 vmin(10);
    }
}