*,
*::before,
*::after {
    box-sizing: inherit;
    margin: 0;
    padding: 0;
}

html {
    box-sizing: border-box;
}

body {
    overflow-x: hidden;
}

.container {
    max-width: vw(1392);
    max-height: 100%;
    margin: 0 auto;
}

button {
    cursor: pointer;
}

a:focus,
a:visited,
a:active,
a:hover,
textarea:focus,
textarea:visited,
textarea:active,
textarea:hover,
input:focus,
input:visited,
input:active,
input:hover,
select:focus,
select:visited,
select:active,
select:hover,
button:focus,
button:visited,
button:active,
button:hover {
    outline: 0;
    outline-style: none;
}

input[type="text"],
input[type="button"],
input[type="submit"],
input[type="checkbox"] {
    -webkit-appearance: none;
    -moz-appearance: none;
    appearance: none;
    border-radius: 0;
}

a {
    display: inline-block;
    text-decoration: none;
}

img {
    max-width: 100%;
}

li {
    list-style: none;
}