.about {
    &__container {
        padding: vw(45) vw(269) vw(100) vw(280);
        @include mobile {
            padding: vw(45) vmin(90) vw(100) vmin(90);
        }
    }
    &__row {
        display: flex;
        flex-wrap: wrap;
        margin: 0 vw(-55);
    }
    .col-6 {
        width: 50%;
        padding: 0 vw(55);
        @include mobile {
            width: 100%;
        }
    }
    &__title {
        margin-top: vw(65);
        margin-bottom: vw(35);
        font-family: $font-family-segoiUI;
        font-size: vw(24);
        font-weight: 700;
        text-transform: uppercase;
        @include mobile {
            font-size: vmin(24);
            text-align: center;
        }
    }
    &__description {
        font-family: $font-family-ProximaNova;
        font-weight: 300;
        font-size: vw(16);
        @include mobile {
            font-size: vmin(19);
        }
    }
    .tab {
        display: flex;
        &-title {
            width: 50%;
            font-family: $font-family-ProximaNova;
            font-size: vw(16);
            font-weight: 400;
            text-align: center;
            padding: vw(10) 0;
            @include mobile {
                font-size: vmin(18);
            }
        }
        &-title.is-active {
            font-weight: 700;
            border-bottom: vw(4) solid red;
            @include mobile {
                border-bottom: vmin(4) solid red;
            }
        }
        @include mobile {
            margin-top: vmin(40);
        }
    }
}