    .menu {
        position: relative;
        &__title {
            @include position(absolute, 50%);
            @include flex-center;
            @include border(0, vw(40));
            @include color($color-orange, $color-white);
            @include sizing(vw(160), vw(53));
            font-family: $font-family-segoiUI;
            font-size: vw(19);
            font-weight: 700;
            transform: translateX(-50%) translateY(-65%);
            text-transform: uppercase;
            @include mobile {
                @include border(0, vmin(40));
                @include sizing(vmin(169), vmin(55), vmin(19));
                transform: translateX(-50%) translateY(-50%);
            }
        }
        &-top {
            position: relative;
            min-height: vw(150);
            max-height: vw(156);
            box-shadow: 0 vw(10) vw(10) rgba(0, 0, 0, 0.1);
            @include mobile {
                min-height: vmin(100);
                max-height: vmin(614);
                box-shadow: 0 vmin(10) vmin(10) rgba(0, 0, 0, 0.1);
            }
        }
        &__tabs-nav {
            padding: vw(35) 0 0;
            img {
                max-height: vw(55);
                @include mobile {
                    max-height: vmin(55);
                }
            }
            @include mobile {
                padding: vmin(55) 0 0;
            }
        }
        img {
            position: relative;
            max-width: 100%;
            width: auto;
        }
        &__name {
            position: relative;
            margin-top: vw(12);
            padding-bottom: vw(10);
            max-height: vw(14);
            text-align: center;
            font-family: $font-family-segoiUI;
            font-size: vw(15);
            font-weight: 600;
            color: #444444;
            @include mobile {
                margin-top: vmin(5);
                margin-bottom: vmin(30);
                max-height: vmin(15);
                font-size: vmin(18);
            }
        }
        &__row {
            display: flex;
            flex-wrap: wrap;
            text-align: center;
            justify-content: center;
        }
        &__row .slider__item {
            width: 20%;
            font-size: vmin(17);
        }
        &__content {
            display: flex;
            justify-content: center;
            flex-wrap: wrap;
            padding: vw(30) 0;
            background-color: $color-background;
            .col-4 {
                padding: 0 vw(15);
                @include mobile {
                    padding: vmin(15) 0;
                }
            }
            &-item {
                width: vw(234);
                background-color: $color-white;
                text-align: center;
                @include mobile {
                    width: vmin(400);
                }
            }
            @include mobile {
                background-color: $color-white;
                flex-direction: column;
                align-items: center;
            }
        }
        .item {
            padding: 0 vw(26) vw(19);
            border-radius: vw(10);
            &__title {
                font-family: $font-family-ProximaNova;
                font-size: vw(17);
                font-weight: 700;
                @include mobile {
                    font-size: vmin(32);
                }
            }
            &__description {
                margin: vw(6) 0 vw(22);
                font-family: $font-family-segoiUI;
                font-size: vw(11.54);
                color: #a4a4a4;
                @include mobile {
                    margin: vmin(6) 0 vmin(22);
                    font-size: vmin(22);
                }
            }
            &__size {
                display: flex;
                justify-content: space-around;
                color: #a4a4a4;
                font-family: $font-family-segoiUI;
                font-size: vw(14);
                padding: 0 vw(15) 0 vw(10);
                span {
                    display: inline-block;
                    text-align: center;
                }
                @include mobile {
                    font-size: vmin(25);
                    padding: 0 vmin(45) 0 vmin(30);
                }
            }
            &__price {
                font-family: $font-family-segoiUI;
                font-size: vw(17);
                font-weight: 600;
                color: #ab1717;
                margin: vw(5) 0 vw(18);
                @include mobile {
                    margin: vmin(5) 0 vmin(35);
                    font-size: vmin(33);
                }
            }
            &__btn-add {
                @include color(#ab1717, #fff);
                @include border(vw(2), vw(30), #ab1717, solid);
                padding: vw(10) vw(50) vw(15);
                min-width: vw(160);
                font-family: $font-family-segoiUI;
                font-size: vw(17);
                font-weight: 600;
                transition: 0.4s;
                cursor: pointer;
                @include desktop {
                    &:hover {
                        @include color(#fff, #ab1717);
                        @include border(vw(2), vw(30), #ab1717, solid)
                    }
                }
                @include mobile {
                    @include border(vmin(2), vmin(30), #ab1717, solid);
                    padding: vmin(25) vmin(50) vmin(34);
                    min-width: vmin(316);
                    font-size: vmin(25);
                }
            }
        }
        @include mobile {
            min-height: vmin(300);
        }
    }
    
    .tab {
        display: none;
    }
    
    .tab.is-active {
        display: flex;
        -webkit-animation: fade 0.4s linear forwards;
        animation: fade 0.4s linear forwards;
    }
    
    @-webkit-keyframes fade {
        0% {
            opacity: 0;
            transform: translateX(2%);
        }
        100% {
            opacity: 1;
            transform: translateX(0);
        }
    }
    
    @keyframes fade {
        0% {
            opacity: 0;
            transform: translateX(2%);
        }
        100% {
            opacity: 1;
            transform: translateX(0);
        }
    }