.social {
	display: flex;

	&__item {
		display: flex;
		align-items: center;
		justify-content: center;
		margin: 0 vmin(15);
		border: 0;
		border-radius: 50%;
		padding: 0;
		width: vmin(26);
		height: vmin(26);
		background-color: $color-black;
		transition: opacity 0.3s;

		svg {
			display: block;
		}

		&--fb svg {
			width: vmin(9);
			height: vmin(16);

			@include desktop {
				margin-top: -0.5px;
				margin-left: -2px;
				width: 9px;
				height: 16px;

				.is-browser-firefox & {
					margin-top: 0;
				}
			}
		}

		&--tw svg {
			margin-left: vmin(1);
			width: vmin(14);
			height: vmin(11);

			@include desktop {
				margin-left: 1px;
				width: 14px;
				height: 11px;
			}
		}

		&--vk svg {
			margin-right: 0;
			width: vmin(15);
			height: vmin(10);

			@include desktop {
				margin-right: -1px;
				width: 15px;
				height: 10px;
			}
		}

		&--ok svg {
			margin-bottom: vmin(2);
			width: vmin(10);
			height: vmin(14);

			@include desktop {
				width: 10px;
				height: 14px;
			}
		}

		@include desktop {
			margin: 0 10px;
			width: 26px;
			height: 26px;
			transition: 0.3s;

			&:hover {
				background-color: $color-cinnabar;
			}

			&:first-child {
				margin-left: 0;
			}
		}
	}

	@include mobile {
		justify-content: center;
		padding: vmin(36) vmin(41) vmin(41);
	}
}
