@mixin flex-center ($align-items:center, $justify-content:center) {
	display: flex;
	align-items: $align-items;
	justify-content: $justify-content;
}

@mixin sizing ($width: auto, $height: auto, $font-size: auto) {
	width: $width;
	height: $height;
	font-size: $font-size;
}

@mixin position ($position ,$left: auto,$right: auto,$top: auto,$bottom: auto) {
	position: $position;
	left: $left;
	right: $right;
	top: $top;
	bottom: $bottom;
}

@mixin border ($border-width: none ,$border-radius: 0, $border-color: $color-black, $border-style: none) {
	border: $border-width;
	border-radius: $border-radius;
	border-color: $border-color;
	border-style: $border-style;
}

@mixin color ($background-color, $text-color: $color-black) {
	background-color: $background-color;
	color: $text-color;
}