.offers {
    &__container {
        padding: vw(40) vw(228);
        @include mobile {
            padding: vmin(40) vmin(0) vmin(40) vmin(50);
        }
    }
    &__title {
        margin-bottom: vw(35);
        text-align: center;
        text-transform: uppercase;
        font-family: $font-family-segoiUI;
        font-size: vw(24);
        font-weight: 700;
        @include mobile {
            font-size: vmin(24);
            margin: 0 0 vmin(30) 0;
            padding: 0 vmin(60) 0 0;
        }
    }
    .slick-slide {
        margin: 0 vmin(15);
        width: vmin(360);
    }
}