.slick-slide {
    outline: none;
}

.slider {
    position: relative;
    .slick-dots {
        @include position (absolute, 50%, auto, auto, vw(60));
        display: flex;
        transform: translateX(-50%);
        & li {
            margin: 0 vw(6);
            &.slick-active button {
                background-color: $color-white;
            }
        }
        & button {
            @include sizing (vw(13), vw(13), 0);
            @include border (none, 50%);
            background-color: $color-gray;
            @include mobile {
                @include sizing (vmin(13), vmin(13), 0);
            }
        }
        @include mobile {
            @include position (absolute, 50%, auto, auto, vmin(45));
        }
    }
    &__item {
        img {
            width: 100%;
            max-height: vw(425);
            @include mobile {
                max-height: vmin(425);
                height: vmin(425);
            }
        }
    }
}

.menu__slider {
    position: relative;
    text-align: center;
    max-width: vw(1220);
    margin: 0 auto;
    overflow: visible;
    .slick-list {
        overflow-x: hidden;
        min-height: 138px;
    }
    .slider__item {
        position: relative;
        cursor: pointer;
        overflow: visible;
        &.is-active::before {
            content: '';
            position: absolute;
            left: 50%;
            bottom: vw(-32);
            z-index: 2;
            width: vw(90);
            height: vw(4);
            min-height: 4px;
            transform: translateX(-50%);
            background-color: #ab1717;
        }
    }
    .slick-arrow {
        position: absolute;
        left: 10%;
    }
    .slick-arrow.slick-prev,
    .slick-arrow.slick-next {
        position: absolute;
        background: url(../../images/prev-btn.png) center no-repeat;
        background-size: cover;
        font-size: 0;
        width: vw(10);
        height: vw(18);
        border: none;
        outline: none;
    }
    .slick-arrow.slick-prev {
        left: vw(-32);
        top: vw(25);
    }
    .slick-arrow.slick-next {
        left: calc(100% + 2%);
        top: vw(25);
        transform: rotate(180deg);
    }
    img {
        display: inline-block;
        width: vw(72);
        height: vw(65);
        @include mobile {
            width: vmin(72);
            height: vmin(65);
        }
    }
}

.offers__slider {
    position: relative;
    margin: 0 vw(1);
    &-item {
        padding: 0 vw(15);
        img {
            border-radius: vw(20);
            @include mobile {
                max-height: vmin(220);
            }
        }
        @include mobile {
            max-height: vmin(220);
            width: vmin(360);
            padding: 0 0;
        }
    }
    .slick-dots {
        @include position (absolute, 49.5%, auto, auto, vw(-45));
        display: flex;
        transform: translateX(-50%);
        & li {
            margin: 0 vw(6);
            &.slick-active button {
                background-color: #ab1717;
            }
        }
        & button {
            @include sizing (vw(13), vw(13), 0);
            @include border (none, 50%);
            background-color: #e8e8e8;
        }
        @include mobile {
            display: none !important;
        }
    }
    @include mobile {
        max-height: vmin(200);
    }
}